.index-main .hero-header .page-wrapper {
  text-align: center;
  color: #fff;
  z-index: 2;
}
.index-main .hero-section {
  background-color: #000;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
}
.index-main .hero-section .hero-content {
  text-align: center;
  color: #fff;
  z-index: 2;
}
.index-main .hero-section .hero-content h1 {
  font-size: 4rem;
  margin-bottom: 2rem;
  color: #fff;
}
.index-main .holder-section {
  padding: 4rem 0;
  background-color: rgba(204, 204, 204, 0.3333333333);
}
.index-main .holder-section .project-section {
  padding: 4rem 0;
}
.index-main .holder-section .project-section .container-project {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
@media (max-width: 991.98px) {
  .index-main .holder-section .project-section .container-project {
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
  }
}
.index-main .holder-section .project-section .container-project .project-content {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
}
.index-main .holder-section .project-section .container-project .project-content .content-title {
  color: black;
  padding: 0 1rem;
}
.index-main .holder-section .project-section .container-project .project-content .content-subtitle {
  padding: 1rem;
}
.index-main .holder-section .project-section .container-project .project-content .content-button {
  display: flex;
  padding: 1rem;
}
.index-main .holder-section .project-section .container-project .project-image {
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.3882352941);
}

.galery-main .galery-section .container-galery {
  padding: 4rem 0;
}
.galery-main .galery-section .container-galery .galery-selection {
  border-bottom: 0.0625rem solid grey;
  gap: 2rem;
  display: flex;
}
.galery-main .galery-section .container-galery .galery-list-item {
  display: flex;
  justify-content: space-around;
  padding: 2rem 0;
}
.galery-main .galery-section .container-galery .galery-list-item .list-active {
  display: block !important;
}
.galery-main .galery-section .container-galery .galery-list-item .list {
  display: none;
  transition: all linear 0.6;
}
.galery-main .galery-section .container-galery .galery-list-item .items {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (max-width: 991.98px) {
  .galery-main .galery-section .container-galery .galery-list-item .items {
    grid-template-columns: repeat(1, 1fr);
  }
}
.galery-main .galery-section .container-galery .galery-list-item .items .item {
  border-radius: 0.3125rem;
  border: 0.0625rem solid rgba(0, 0, 0, 0.26);
  padding: 2rem;
  background-color: rgba(255, 255, 255, 0.266);
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1rem;
  flex-wrap: wrap;
}
.galery-main .galery-section .container-galery .galery-list-item .items .item img {
  width: 100%;
}
.galery-main .galery-section .container-galery .galery-list-item .items .item .technologies {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}
.galery-main .galery-section .container-galery .galery-list-item .items .item .technologies a:nth-child(n) {
  padding: 0.125rem 1.125rem;
  border-radius: 1.125rem;
  color: black;
}
.galery-main .galery-section .container-galery .galery-list-item .items .item .technologies .CSS {
  background-color: rgba(0, 99, 145, 0.405);
}
.galery-main .galery-section .container-galery .galery-list-item .items .item .technologies .SASS {
  background-color: rgba(252, 5, 124, 0.405);
}
.galery-main .galery-section .container-galery .galery-list-item .items .item .technologies .PHP {
  background-color: rgba(72, 1, 117, 0.405);
}
.galery-main .galery-section .container-galery .galery-list-item .items .item .technologies .Laravel {
  background-color: rgba(255, 0, 0, 0.405);
}
.galery-main .galery-section .container-galery .galery-list-item .items .item .technologies .Webpack {
  background-color: rgba(73, 197, 255, 0.405);
}
.galery-main .galery-section .container-galery .galery-list-item .items .item .technologies .Bootstrap {
  background-color: rgba(65, 0, 145, 0.405);
}
.galery-main .galery-section .container-galery .galery-list-item .items .item .technologies .HTML {
  background-color: rgba(255, 132, 0, 0.46);
}
.galery-main .galery-section .container-galery .galery-list-item .items .item .technologies .JavaScript {
  background-color: rgba(234, 255, 0, 0.405);
}
.galery-main .galery-section .container-galery .galery-list-item .items .item .technologies .Node {
  background-color: rgba(50, 255, 47, 0.405);
}
.galery-main .galery-section .container-galery .galery-list-item .items .item .technologies .React {
  background-color: rgba(47, 189, 255, 0.405);
}

.contact-main {
  position: relative;
}
.contact-main .contact-section {
  padding: 7rem 0;
}
.contact-main .contact-section .container-contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  color: black;
}
.contact-main .contact-section .container-contact .form-contact {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1rem;
  width: 28rem;
  padding: 2rem;
  background: rgba(0, 0, 0, 0);
  color: black;
  border: 0.0625rem solid black;
  border-radius: 0.3125rem;
}
.contact-main .contact-section .container-contact .form-contact .form-controller {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 0.3rem;
}
.contact-main .contact-section .container-contact .form-contact .form-controller .form-input {
  border: 0.0625rem solid black;
  border-radius: 0.125rem 0.625rem;
  padding: 0.25rem 0.625rem;
  background: rgba(0, 0, 0, 0);
}
.contact-main .contact-section .container-contact .form-contact .form-controller .form-input--textarea {
  height: 4rem;
}

.blog-main {
  padding: 4rem;
}
.blog-main .blog-section {
  padding: 4rem 0;
}
.blog-main .blog-section .container-blog {
  display: flex;
  padding: 4rem 0;
}